* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Bree Serif', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Bree Serif', serif;
}

.card {
    background: rgb(7, 45, 71);
    border-radius: 2px;
    height: 379px;
    margin: 1rem;
    position: relative;
    width: 275px;
    border-radius: 5px;
    /* box-shadow: 0 3px 6px #999, 0 3px 6px #999; */
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: rgb(7, 45, 71);
  }
  .card > .img-container > img {
    width: 100%;
    height: 92%;
    padding-top: 2em;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }

.wrapper {
    padding-top: 50px;
    background: rgb(4, 121, 4);
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.header {
  width: 100%;
  font-size: 2rem;
  color: rgb(255, 255, 255);
  margin-bottom: 10px;
}

.title {
  float: left;
}

.scores {
  float: right;
  margin-right: 50px;
}
